.card {
  /* height: 300px; */
  width: 220px;
  background-color: white;
  border-radius: 5px;
  border: 2px solid #bab9b3;
  outline: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  box-shadow: 0px 0px 10px 3px #bab9b3;
  /* cursor: pointer; */
  animation: show 1s ease;
  transition: all 0.2s linear;
  position: relative;
}
.card:hover {
  border: 2px solid blueviolet;
}
@keyframes show {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.card__img {
  height: 220px;
  width: 220px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  object-fit: cover;
}
.card-like {
  /* background-color: coral; */
  position: absolute;
  top: 5px;
  left: 8px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.card-discount {
  background-color: rgb(247, 32, 32);
  position: absolute;
  top: 5px;
  right: 5px;
  width: 50px;
  height: 22px;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Roboto";
  font-size: 15px;
  color: white;
}
.card-tags__new {
  background: linear-gradient(to bottom, #22a6d6 5%, #3bd5ed 100%);
  position: absolute;
  top: 5px;
  right: calc(50% - 30px);
  width: 60px;
  height: 25px;
  border-radius: 8px;
  color: white;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-tags__sale{
  background-color: red;
  position: absolute;
  top: 208px;
  right: 5px;
  width: 55px;
  height: 25px;
  border-radius: 8px;
  color: white;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card__wight {
  height: 17px;
  width: 200px;
  /* background-color: chartreuse; */
  font-family: "Roboto";
  font-size: 13px;
  color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.card__title {
  /* background-color: crimson; */
  height: 60px;
  width: 220px;
  display: flex;
  justify-content: center;
  border-top: 1px solid grey;
  align-items: center;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 500;
  text-align: center;
}
.card__title-fuchsia{
  background-color: fuchsia;
 }
.card-wpapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-wpapper__price,
.card-wpapper-box {
  height: 40px;
  width: 110px;
  /* background-color: coral; */
  font-family: "Roboto";
  font-size: 15px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.new {
  color: red;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.card-wpapper__price-old {
  color: black;
  position: absolute;
  top: 0;
  left: 5px;
  font-size: 13px;
  opacity: 0.5;
  text-decoration: line-through;
}
.card-wpapper__button {
  height: 30px;
  width: 100px;
  background-color: lawngreen;
  border: 1px solid grey;
  border-radius: 10px;
  color: black;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s linear;
}
.card-wpapper__button:hover {
  transform: scale(1.02);
}
.card-wpapper__button:active {
  transform: scale(0.97);
}
.card-wpapper__button:disabled{
  opacity: 0.6;
}
