.signup {
  /* background-color: aqua; */
  margin-top: 25px;
  max-width: 1100px;
  width: 90vw;
  min-height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.signup-title {
  /* background-color: aqua; */
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 35px;
  color: rgba(0, 0, 0, 0.88);
}
.signup-form {
  /* background-color: coral; */
  width: 300px;
  display: flex;
  flex-direction: column;
}
.signup-form__label {
  margin: 15px 10px 10px 10px;
  height: 20px;
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 20px;
  color: rgba(0, 0, 0, 0.88);
  position: relative;
}
.signup-form__label::before {
  content: "*";
  position: absolute;
  top: 0;
  left: -10px;
  color: red;
}
.signup-form__input {
  background-color: white;
  color: rgba(0, 0, 0, 0.88);
  padding: 4px 11px;
  font-size: 17px;
  line-height: 1.57;
  outline: none;
  border-radius: 6px;
  border: 1px solid #d9d9d9;
  transition: all 0.2s;
}
.signup-form__button {
  height: 40px;
  margin: 20px 0px 10px 0px;
  border-radius: 5px;
  border: 1px solid grey;
  background-color: chartreuse;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 18px;
  transition: all 0.2s linear;
}
.signup-form-error {
  color: red;
  margin-left: 10px;
  margin-right: 10px;
}
.signup-form__message {
  margin: 10px 10px 10px 10px;
  color: green;
  text-align: left;
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 15px;
}
.signup-form__button:hover {
  transform: scale(1.02);
}
.signup-form__button:active {
  transform: scale(0.97);
}
